import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        switchToStartGame: false,
        isMoveAvtive: false,
        isRolesRunning: false,
        isRolesJump: false,
        easeOut: true,
        switchOpenObstacle: true,
        electricPower: false,
        thePowerIndication: false,
        resetTheGame: false,
        runSpeed: 1,
        runSpeedLevel: 0,
        addRunSpeed: false,
        totalScore: 0,
        Ghost: false,
    },
    mutations: {
        chengeTheStyAvtive(state) {
            state.isMoveAvtive = !state.isMoveAvtive;
        },
        stopTheRunningActive(state) {
            state.isRolesRunning = !state.isRolesRunning;
        },
        transitionEase(state) {
            state.easeOut = !state.easeOut;
        },
        playTheGame(state) {
            state.resetTheGame = false;
            setTimeout(() => {
                state.resetTheGame = true;
            }, 50);
            setTimeout(() => {
                state.Ghost = false;
                state.isMoveAvtive = true;
                state.isRolesRunning = true;
                state.isRolesJump = true;
                state.switchToStartGame = true;
                state.thePowerIndication = true;
                state.totalScore = 0;
                state.runSpeed = 1;
                state.addRunSpeed = false;
            }, 100);
        },
        countToScore(state) {
            state.totalScore += 10;
        },
        changeRunSpeed(state, v) {
            state.runSpeed = v;
        },
        addTheRunSpeed(state, v) {
            state.addRunSpeed = v;
        },
        changeRunSpeedLevel(state, v) {
            state.runSpeedLevel = v;
        },
        changeSwitchToObstacle(state) {
            state.switchOpenObstacle = !state.switchOpenObstacle;
        },
        electricToShock(state) {
            if (state.thePowerIndication && state.isMoveAvtive) {
                state.thePowerIndication = false;
                state.electricPower = !state.electricPower;
                setTimeout(() => {
                    state.electricPower = !state.electricPower;
                }, 600);
                setTimeout(() => {
                    if (state.isMoveAvtive) {
                        state.thePowerIndication = true;
                    }
                }, 3000);
            } else {
                return
            }
        },
        stopTheGrass(state) {
            state.switchToStartGame = false;;
        },
        showTheGhost(state) {
            state.Ghost = true;
        },
    },
})

export default store;


