<template>
  <div class="Haunter-wrap">
    <div class="HaunterLocationPoint"></div>
    <div
      class="Haunter"
      :class="{'hideHaunter':electricPower,'lightningHideHaunter':lightningHideHaunter,'HaunterNone':HaunterNone}"
    ></div>
    <div
      class="HaunterImg"
      :class="{'animation':charred==false,'charred':charred==true,'HaunterNone':HaunterNone}"
    ></div>
    <div
      class="HaunterLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'HaunterNone':HaunterNone}"
    ></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      HaunterPosition: 810,
      HaunterImgPosition: 800,
      lightningHideHaunter: false,
      charred: false,
      HaunterNone: false
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "electricPower",
      "switchGhost"
    ])
  },
  // watch: {
  //   // switchToStartGame(v) {
  //   //   if (v) {
  //   //     this.randomTheHaunter();
  //   //   }
  //   // },
  //   switchGhost() {
  //     console.log("啟動");
  //     this.randomTheHaunter();
  //   }
  // },
  methods: {
    randomTheHaunter() {
      this.HaunterNone = Math.floor(Math.random() * (1 - 0 + 1) + 0);
      console.log("鬼斯通" + this.HaunterNone);
      if (this.HaunterNone !== 1 ) {
        this.HaunterNone = false;
      } else {
        this.HaunterNone = true;
      }
      this.startTheHaunter();
    },
    startTheHaunter() {
      let vm = this;
      let HaunterLocationPoint = document.querySelector(
        ".HaunterLocationPoint"
      );
      let Haunter = document.querySelector(".Haunter");
      let HaunterImg = document.querySelector(".HaunterImg");
      let HaunterLightningStrikes = document.querySelector(
        ".HaunterLightningStrikes"
      );
      vm.HaunterTime = setInterval(() => {
        this.HaunterPosition -= this.runSpeed + 0.2;
        this.HaunterImgPosition -= this.runSpeed + 0.2;
        HaunterLocationPoint.style.left = this.HaunterPosition + "px";
        Haunter.style.left = this.HaunterPosition + "px";
        HaunterImg.style.left = this.HaunterImgPosition + "px";
        HaunterLightningStrikes.style.left = this.HaunterImgPosition + "px";
        this.moveJudge();
        this.LightningStrikesJudge();
        if (this.HaunterPosition < -100) {
          clearInterval(vm.HaunterTime);
          this.HaunterPosition = 810;
          this.HaunterImgPosition = 800;
          this.lightningHideHaunter = false;
          this.charred = false;
          this.randomTheHaunter();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.HaunterTime);
        }
      }, 0);
    },
    moveJudge() {
      let vm = this;
      let Haunter = document.querySelector(".Haunter");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let HaunterLeft = Haunter.offsetLeft;
      let HaunterRight = HaunterLeft + Haunter.offsetWidth;
      let HaunterTop = Haunter.offsetTop;
      let HaunterBottom = HaunterTop + Haunter.offsetHeight;
      if (
        raichuRight < HaunterLeft ||
        HaunterRight < raichuLeft ||
        raichuBottom < HaunterTop ||
        HaunterBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.HaunterTime);
      }
    },
    LightningStrikesJudge() {
      let vm = this;
      let HaunterLightningStrikes = document.querySelector(
        ".HaunterLightningStrikes"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let HaunterLightningStrikesLeft = HaunterLightningStrikes.offsetLeft;
      let HaunterLightningStrikesRight =
        HaunterLightningStrikesLeft + HaunterLightningStrikes.offsetWidth;
      let HaunterLightningStrikesTop = HaunterLightningStrikes.offsetTop;
      let HaunterLightningStrikesBottom =
        HaunterLightningStrikesTop + HaunterLightningStrikes.offsetHeight;
      if (
        raichuRight < HaunterLightningStrikesLeft ||
        HaunterLightningStrikesRight < raichuLeft ||
        raichuBottom < HaunterLightningStrikesTop ||
        HaunterLightningStrikesBottom < raichuTop
      ) {
        return;
      } else {
        this.lightningHideHaunter = true;
        this.charred = true;
      }
    },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let HaunterTime;
    let HaunterLightningStrikesTime;
    this.randomTheHaunter();
  }
};
</script>

<style lang="scss" scoped>
.HaunterImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/haunter01.png);
  position: absolute;
  left: 800px;
  // left: 300px;
  // bottom: 25px;
  bottom: 105px;
  width: 50px;
  height: 50px;
  // z-index: 1000;
  // border: 2px solid #000000;
}

.HaunterImg.animation {
  animation: HaunterRunning 0.8s infinite steps(1, start);
}

@keyframes HaunterRunning {
  0% {
    background-image: url(./assets/img/haunter01.png);
  }
  25% {
    background-image: url(./assets/img/haunter02.png);
  }
  50% {
    background-image: url(./assets/img/haunter01.png);
  }
  75% {
    background-image: url(./assets/img/haunter03.png);
  }
  100% {
    background-image: url(./assets/img/haunter01.png);
  }
}

.HaunterImg.charred {
  background-image: url(./assets/img/charredHaunter.png);
}

.Haunter {
  position: absolute;
  // left: 310px;
  left: 810px;
  // bottom: 30px;
  bottom: 110px;
  width: 20px;
  height: 30px;
  z-index: 1000;
  // border: 2px solid #000000;
}

.Haunter.hideHaunter {
  display: none;
}

.Haunter.lightningHideHaunter {
  display: none;
}

.HaunterLightningStrikes {
  position: absolute;
  // left: 295px;
  left: 800px;
  // bottom: 20px;
  bottom: 100px;
  width: 55px;
  height: 55px;
  z-index: 1000;
  // border: 2px solid orange;
}

.showLightningStrikes {
  display: none;
}

.HaunterLocationPoint {
  width: 10px;
  height: 10px;
  // background: orange;
  position: absolute;
  left: 800px;
  top: 10px;
}

.Haunter-wrap .Haunter.HaunterNone,
.Haunter-wrap .HaunterImg.HaunterNone,
.Haunter-wrap .HaunterLightningStrikes.HaunterNone {
  display: none;
}
</style>


