<template>
  <div class="Geodude-wrap">
    <div class="GeodudeLocationPoint"></div>
    <div class="Geodude" :class="{'GeodudeNone':GeodudeNone}"></div>
    <div class="GeodudeImg animation" :class="{'GeodudeNone':GeodudeNone}"></div>

    <div class="Geodude2" :class="{'GeodudeNone':GeodudeNone2}"></div>
    <div class="GeodudeImg2 animation" :class="{'GeodudeNone':GeodudeNone2}"></div>
    <!-- <div
      class="GeodudeLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'GeodudeNone':GeodudeNone}"
    ></div>-->
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      GeodudePosition: 710,
      GeodudeImgPosition: 700,
      GeodudePosition2: 1010,
      GeodudeImgPosition2: 1000,
      // lightningHideGeodude: false,
      // charred: false,
      GeodudeNone: false,
      GeodudeNone2: false,
      GeodudeNum1: 0,
      GeodudeNum2: 0
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning"
      // "electricPower",
      // "switchGhost"
    ])
  },
  watch: {
    switchToStartGame(v) {
      if (v) {
        this.randomTheGeodude();
      }
    }
    // switchGhost() {
    //   this.randomTheGeodude();
    // }
  },
  methods: {
    randomTheGeodude() {
      this.GeodudeNum = Math.floor(Math.random() * (3 - 0 + 1) + 0);
      this.GeodudeNum2 = Math.floor(Math.random() * (3 - 0 + 1) + 0);
      console.log("小拳石" + this.GeodudeNum);
      console.log("小拳石" + this.GeodudeNum2);
      if (this.GeodudeNum !== 0) {
        this.GeodudeNone = false;
      } else {
        this.GeodudeNone = true;
      }
      if (this.GeodudeNum2 !== 0) {
        this.GeodudeNone2 = false;
      } else {
        this.GeodudeNone2 = true;
      }
      this.startTheGeodude();
    },
    startTheGeodude() {
      let vm = this;
      let GeodudeLocationPoint = document.querySelector(
        ".GeodudeLocationPoint"
      );
      let Geodude = document.querySelector(".Geodude");
      let GeodudeImg = document.querySelector(".GeodudeImg");
      let Geodude2 = document.querySelector(".Geodude2");
      let GeodudeImg2 = document.querySelector(".GeodudeImg2");
      // let GeodudeLightningStrikes = document.querySelector(
      //   ".GeodudeLightningStrikes"
      // );
      vm.GeodudeTime = setInterval(() => {
        this.GeodudePosition -= this.runSpeed + 0.1;
        this.GeodudeImgPosition -= this.runSpeed + 0.1;
        this.GeodudePosition2 -= this.runSpeed + 0.1;
        this.GeodudeImgPosition2 -= this.runSpeed + 0.1;
        GeodudeLocationPoint.style.left = this.GeodudePosition + "px";
        Geodude.style.left = this.GeodudePosition + "px";
        GeodudeImg.style.left = this.GeodudeImgPosition + "px";
        Geodude2.style.left = this.GeodudePosition2 + "px";
        GeodudeImg2.style.left = this.GeodudeImgPosition2 + "px";
        // GeodudeLightningStrikes.style.left = this.GeodudeImgPosition + "px";
        this.moveJudge();
        this.moveJudge2();
        // this.LightningStrikesJudge();
        if (this.GeodudePosition2 < -100) {
          clearInterval(vm.GeodudeTime);
          this.GeodudePosition = 710;
          this.GeodudeImgPosition = 700;
          this.GeodudePosition2 = 1010;
          this.GeodudeImgPosition2 = 1000;
          // this.lightningHideGeodude = false;
          // this.charred = false;
          this.randomTheGeodude();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.GeodudeTime);
        }
      }, 0);
    },
    moveJudge() {
      let vm = this;
      let Geodude = document.querySelector(".Geodude");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let GeodudeLeft = Geodude.offsetLeft;
      let GeodudeRight = GeodudeLeft + Geodude.offsetWidth;
      let GeodudeTop = Geodude.offsetTop;
      let GeodudeBottom = GeodudeTop + Geodude.offsetHeight;
      if (
        raichuRight < GeodudeLeft ||
        GeodudeRight < raichuLeft ||
        raichuBottom < GeodudeTop ||
        GeodudeBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.GeodudeTime);
      }
    },
    moveJudge2() {
      let vm = this;
      let Geodude2 = document.querySelector(".Geodude2");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let Geodude2Left = Geodude2.offsetLeft;
      let Geodude2Right = Geodude2Left + Geodude2.offsetWidth;
      let Geodude2Top = Geodude2.offsetTop;
      let Geodude2Bottom = Geodude2Top + Geodude2.offsetHeight;
      if (
        raichuRight < Geodude2Left ||
        Geodude2Right < raichuLeft ||
        raichuBottom < Geodude2Top ||
        Geodude2Bottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.GeodudeTime);
      }
    },
    // LightningStrikesJudge() {
    //   let vm = this;
    //   let GeodudeLightningStrikes = document.querySelector(
    //     ".GeodudeLightningStrikes"
    //   );
    //   let raichuLeft = raichu.offsetLeft;
    //   let raichuRight = raichuLeft + raichu.offsetWidth;
    //   let raichuTop = raichu.offsetTop;
    //   let raichuBottom = raichuTop + raichu.offsetHeight;
    //   let GeodudeLightningStrikesLeft = GeodudeLightningStrikes.offsetLeft;
    //   let GeodudeLightningStrikesRight =
    //     GeodudeLightningStrikesLeft + GeodudeLightningStrikes.offsetWidth;
    //   let GeodudeLightningStrikesTop = GeodudeLightningStrikes.offsetTop;
    //   let GeodudeLightningStrikesBottom =
    //     GeodudeLightningStrikesTop + GeodudeLightningStrikes.offsetHeight;
    //   if (
    //     raichuRight < GeodudeLightningStrikesLeft ||
    //     GeodudeLightningStrikesRight < raichuLeft ||
    //     raichuBottom < GeodudeLightningStrikesTop ||
    //     GeodudeLightningStrikesBottom < raichuTop
    //   ) {
    //     return;
    //   } else {
    //     this.lightningHideGeodude = true;
    //     this.charred = true;
    //   }
    // },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let GeodudeTime;
    // let GeodudeLightningStrikesTime;
    // this.randomTheGeodude();
  }
};
</script>

<style lang="scss" scoped>
// 小拳石-圖片
.GeodudeImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Geodude01.png);
  position: absolute;
  left: 700px;
  // left: 300px;
  bottom: 22px;
  width: 42px;
  height: 42px;
  // z-index: 1000;
  border: 2px solid #000000;
}

// 小拳石-移動效果
.GeodudeImg.animation {
  animation: GeodudeRunning 0.8s infinite steps(1, start);
}
@keyframes GeodudeRunning {
  0% {
    background-image: url(./assets/img/Geodude01.png);
  }
  25% {
    background-image: url(./assets/img/Geodude02.png);
  }
  50% {
    background-image: url(./assets/img/Geodude03.png);
  }
  75% {
    background-image: url(./assets/img/Geodude02.png);
  }
  100% {
    background-image: url(./assets/img/Geodude01.png);
  }
}

// 小拳石-碰撞判定區塊
.Geodude {
  position: absolute;
  left: 1010px;
  bottom: 27px;
  width: 20px;
  height: 30px;
  z-index: 1000;
  border: 2px solid #000000;
}

// 小拳石2-圖片
.GeodudeImg2 {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Geodude01.png);
  position: absolute;
  left: 1000px;
  // left: 300px;
  bottom: 22px;
  width: 42px;
  height: 42px;
  // z-index: 1000;
  border: 2px solid #000000;
}

// 小拳石2-移動效果
.GeodudeImg2.animation {
  animation: GeodudeRunning 0.8s infinite steps(1, start);
}
// @keyframes GeodudeRunning2 {
//   0% {
//     background-image: url(./assets/img/Geodude01.png);
//   }
//   25% {
//     background-image: url(./assets/img/Geodude02.png);
//   }
//   50% {
//     background-image: url(./assets/img/Geodude03.png);
//   }
//   75% {
//     background-image: url(./assets/img/Geodude02.png);
//   }
//   100% {
//     background-image: url(./assets/img/Geodude01.png);
//   }
// }

// 小拳石2-碰撞判定區塊
.Geodude2 {
  position: absolute;
  left: 1010px;
  bottom: 27px;
  width: 20px;
  height: 30px;
  z-index: 1000;
  border: 2px solid #000000;
}

// .Geodude.hideGeodude {
//   display: none;
// }

// .Geodude.lightningHideGeodude {
//   display: none;
// }

// .GeodudeLightningStrikes {
//   position: absolute;
//   left: 600px;
//   bottom: 17px;
//   width: 55px;
//   height: 55px;
//   z-index: 1000;
//   border: 2px solid orange;
// }

// .showLightningStrikes {
//   display: none;
// }

// 小拳石-移動位置與隱藏定位點
.GeodudeLocationPoint {
  width: 10px;
  height: 10px;
  background: red;
  position: absolute;
  top: 10px;
}

.Geodude-wrap .Geodude.GeodudeNone,
.Geodude-wrap .GeodudeImg.GeodudeNone,
.Geodude-wrap .Geodude2.GeodudeNone,
.Geodude-wrap .GeodudeImg2.GeodudeNone{
  display: none;
}
</style>


