<template>
  <div class="Gengar-wrap">
    <div class="GengarLocationPoint"></div>
    <div
      class="Gengar"
      :class="{'hideGengar':electricPower,'lightningHideGengar':lightningHideGengar,'GengarNone':GengarNone}"
    ></div>
    <div
      class="GengarImg"
      :class="{'animation':charred==false,'charred':charred==true,'GengarNone':GengarNone}"
    ></div>
    <div
      class="GengarLightningStrikes"
      :class="{'showLightningStrikes':!electricPower,'GengarNone':GengarNone}"
    ></div>
  </div>
</template> 

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      GengarPosition: 610,
      GengarImgPosition: 600,
      lightningHideGengar: false,
      charred: false,
      GengarNone: false
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "isRolesRunning",
      "electricPower",
      "switchGhost"
    ])
  },
  // watch: {
  //   // switchToStartGame(v) {
  //   //   if (v) {
  //   //     this.randomTheGengar();
  //   //   }
  //   // },
  //   switchGhost() {
  //     this.randomTheGengar();
  //   }
  // },
  methods: {
    randomTheGengar() {
      this.GengarNone = Math.floor(Math.random() * (2 - 0 + 1) + 0);
      console.log("耿鬼" + this.GengarNone);
      if (this.GengarNone !== 1) {
        this.GengarNone = false;
      } else {
        this.GengarNone = true;
      }
      this.startTheGengar();
    },
    startTheGengar() {
      let vm = this;
      let GengarLocationPoint = document.querySelector(".GengarLocationPoint");
      let Gengar = document.querySelector(".Gengar");
      let GengarImg = document.querySelector(".GengarImg");
      let GengarLightningStrikes = document.querySelector(
        ".GengarLightningStrikes"
      );
      vm.GengarTime = setInterval(() => {
        this.GengarPosition -= this.runSpeed + 0.05;
        this.GengarImgPosition -= this.runSpeed + 0.05;
        GengarLocationPoint.style.left = this.GengarPosition + "px";
        Gengar.style.left = this.GengarPosition + "px";
        GengarImg.style.left = this.GengarImgPosition + "px";
        GengarLightningStrikes.style.left = this.GengarImgPosition + "px";
        this.moveJudge();
        this.LightningStrikesJudge();
        if (this.GengarPosition < -100) {
          clearInterval(vm.GengarTime);
          this.GengarPosition = 610;
          this.GengarImgPosition = 600;
          this.lightningHideGengar = false;
          this.charred = false;
          this.randomTheGengar();
        }
        if (!this.isMoveAvtive) {
          clearInterval(vm.GengarTime);
        }
      }, 0);
    },
    moveJudge() {
      let vm = this;
      let Gengar = document.querySelector(".Gengar");
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let GengarLeft = Gengar.offsetLeft;
      let GengarRight = GengarLeft + Gengar.offsetWidth;
      let GengarTop = Gengar.offsetTop;
      let GengarBottom = GengarTop + Gengar.offsetHeight;
      if (
        raichuRight < GengarLeft ||
        GengarRight < raichuLeft ||
        raichuBottom < GengarTop ||
        GengarBottom < raichuTop
      ) {
        return;
      } else {
        // 碰撞到障礙物，背景停止移動
        vm.chengeTheStyAvtive();
        // 碰撞到障礙物，角色停止動作
        vm.stopTheRunningActive();
        // 碰撞到障礙物，障礙物停止移動
        clearInterval(vm.GengarTime);
      }
    },
    LightningStrikesJudge() {
      let vm = this;
      let GengarLightningStrikes = document.querySelector(
        ".GengarLightningStrikes"
      );
      let raichuLeft = raichu.offsetLeft;
      let raichuRight = raichuLeft + raichu.offsetWidth;
      let raichuTop = raichu.offsetTop;
      let raichuBottom = raichuTop + raichu.offsetHeight;
      let GengarLightningStrikesLeft = GengarLightningStrikes.offsetLeft;
      let GengarLightningStrikesRight =
        GengarLightningStrikesLeft + GengarLightningStrikes.offsetWidth;
      let GengarLightningStrikesTop = GengarLightningStrikes.offsetTop;
      let GengarLightningStrikesBottom =
        GengarLightningStrikesTop + GengarLightningStrikes.offsetHeight;
      if (
        raichuRight < GengarLightningStrikesLeft ||
        GengarLightningStrikesRight < raichuLeft ||
        raichuBottom < GengarLightningStrikesTop ||
        GengarLightningStrikesBottom < raichuTop
      ) {
        return;
      } else {
        this.lightningHideGengar = true;
        this.charred = true;
      }
    },
    ...mapMutations(["chengeTheStyAvtive", "stopTheRunningActive"])
  },
  mounted() {
    let GengarTime;
    let GengarLightningStrikesTime;
    this.randomTheGengar();
  }
};
</script>

<style lang="scss" scoped>
.GengarImg {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-image: url(./assets/img/Gengar01.png);
  position: absolute;
  left: 500px;
  // left: 300px;
  bottom: 22px;
  width: 50px;
  height: 50px;
  // z-index: 1000;
  // border: 2px solid #000000;
}

.GengarImg.animation {
  animation: GengarRunning 0.8s infinite steps(1, start);
}

@keyframes GengarRunning {
  0% {
    background-image: url(./assets/img/Gengar01.png);
  }
  25% {
    background-image: url(./assets/img/Gengar02.png);
  }
  50% {
    background-image: url(./assets/img/Gengar03.png);
  }
  75% {
    background-image: url(./assets/img/Gengar02.png);
  }
  100% {
    background-image: url(./assets/img/Gengar01.png);
  }
}

.GengarImg.charred {
  background-image: url(./assets/img/charredGengar.png);
}

.Gengar {
  position: absolute;
  // left: 310px;
  left: 510px;
  bottom: 27px;
  width: 20px;
  height: 30px;
  z-index: 1000;
  // border: 2px solid #000000;
}

.Gengar.hideGengar {
  display: none;
}

.Gengar.lightningHideGengar {
  display: none;
}

.GengarLightningStrikes {
  position: absolute;
  // left: 295px;
  left: 500px;
  bottom: 17px;
  width: 55px;
  height: 55px;
  z-index: 1000;
  // border: 2px solid orange;
}

.showLightningStrikes {
  display: none;
}

.GengarLocationPoint {
  width: 10px;
  height: 10px;
  // background: #000;
  position: absolute;
  left: 700px;
  top: 10px;
}

.Gengar-wrap .Gengar.GengarNone,
.Gengar-wrap .GengarImg.GengarNone,
.Gengar-wrap .GengarLightningStrikes.GengarNone {
  display: none;
}
</style>


