<template>
  <div>
    <div
      class="seaSide-grass"
      :class="{'avtive-grass':this.isMoveAvtive,'avtive-grass stop-avtive-grass':!this.isMoveAvtive}"
    ></div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      moveGrassSpeed: 0
      // runSpeedLevel: 1,
    };
  },
  computed: {
    ...mapState([
      "isMoveAvtive",
      "runSpeed",
      "switchToStartGame",
      "totalScore",
      "runSpeedLevel"
    ])
  },
  watch: {
    switchToStartGame(v) {
      if (v) {
        this.moveTheGrass();
        this.countTime();
      }
    }
  },
  methods: {
    moveTheGrass() {
      let vm = this;
      let seaSideGrass = document.querySelector(".seaSide-grass");
      vm.moveGrassTime = setInterval(() => {
        // if (!this.isMoveAvtive) {
        //   clearInterval(vm.moveGrassTime);
        //   this.stopTheGrass();
        // }
        // this.moveGrassSpeed -= this.runSpeed;
        // seaSideGrass.style.left = `${this.moveGrassSpeed}px`;
        // if (this.moveGrassSpeed <= -637) {
        //   this.moveGrassSpeed = 0;
        // }
        if (this.totalScore == 400) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(1);
        }
        if (this.totalScore == 800) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(2);
        }
        if (this.totalScore == 1200) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(3);
        }
        if (this.totalScore == 2000) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(4);
        }
        if (this.totalScore == 2400) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(5);
        }
        if (this.totalScore == 3000) {
          this.addTheRunSpeed(true);
          this.changeRunSpeedLevel(6);
        }
        if (this.runSpeedLevel == 2) {
          this.showTheGhost();
        }
        this.addTheRunSpeed(false);
      }, 0);
    },
    countTime() {
      let vm = this;
      vm.countScoreTime = setInterval(() => {
        if (!this.isMoveAvtive) {
          clearInterval(vm.countScoreTime);
          this.stopTheGrass();
        }
        this.countToScore();
      }, 250);
    },
    ...mapMutations([
      "countToScore",
      "changeRunSpeed",
      "changeSwitchToObstacle",
      "stopTheGrass",
      "addTheRunSpeed",
      "changeRunSpeedLevel",
      "showTheGhost",
      "addTheRunSpeed"
    ])
  },
  mounted() {
    let vm = this;
    let moveGrassTime;
    let countScoreTime;
    let seaSideGrass = document.querySelector(".seaSide-grass");
  }
};
</script>

<style lang="scss" scoped>
.seaSide-grass {
  width: 1600px;
  height: 100px;
  background-image: url(./assets/img/seasideGrass.png);
  background-position: bottom left;
  background-size: contain;
  //   background-repeat: no-repeat;
  position: absolute;
  // left: -758px;
  left: 0;
  bottom: 0;
}

.avtive-grass {
  animation-name: moveGrass;
  // animation-duration: 2.55555s;
  animation-duration: 3s;
  animation-fill-mode: none;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes moveGrass {
  0% {
    left: 0;
  }
  100% {
    left: -758px;
  }
}

.seaSide-grass.avtive-grass.stop-avtive-grass {
  animation-play-state: paused;
}
</style>


