<template>
  <div>
    <div id="app" v-show="isNotUpright">
      <div class="app-wrap">
        <PlayGameBtn />
        <SeasideBg />
        <Grassland />
        <!-- <ObstacleTree v-if="resetTheGame" /> -->
        <ObstacleGeodude v-if="resetTheGame"/>
        <!-- <ObstacleHaunter v-if="Ghost" /> -->
        <!-- <ObstacleGengar v-if="Ghost" /> -->
        <RolesThunder />
        <Dashboard />
      </div>
      <ControlGroup />
    </div>
    <div class="remindScreen-wrap" v-show="!isNotUpright">
      <div class="remindScreen"></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import SeasideBg from "./SeasideBg";
import Grassland from "./Grassland";
import RolesThunder from "./RolesThunder";
import ControlGroup from "./ControlGroup";
import ObstacleTree from "./ObstacleTree";
import ObstacleGeodude from './ObstacleGeodude'
import ObstacleHaunter from "./ObstacleHaunter";
import ObstacleGengar from "./ObstacleGengar";
import Dashboard from "./Dashboard";
import PlayGameBtn from "./PlayGameBtn";
export default {
  data() {
    return {
      isNotUpright: false
    };
  },
  components: {
    SeasideBg,
    Grassland,
    RolesThunder,
    ControlGroup,
    ObstacleTree,
    ObstacleGeodude,
    ObstacleHaunter,
    ObstacleGengar,
    Dashboard,
    PlayGameBtn
  },
  computed: {
    ...mapState(["resetTheGame", "Ghost"])
  },
  mounted() {
    const widthView = document.documentElement.clientWidth;
    const heightView = document.documentElement.clientHeight;
    if (widthView > heightView) {
      this.isNotUpright = true;
    }
  }
};
</script>


<style lang="scss" scoped>
#app {
  font-family: "微軟正黑體";
  user-select: none;
  max-width: 600px;
  margin: calc((100vh -240px) / 5) auto 0;
}
.app-wrap {
  width: 100%;
  position: relative;
  // overflow: hidden;
  border-radius: 10px;
  box-shadow: 0 0 5px #000000, 0 0 5px #000000, 0 0 5px #000000, 0 0 5px #000000;
}

.remindScreen-wrap{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 80vh;
}
.remindScreen {
  background-image: url(./assets/img/remindScreen.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 80%;
  height: 80%;
}

@media (max-width: 815px) {
  #app {
    margin: calc((100vh -240px) / 9) auto 0;
  }
}

@media (max-width: 740px) {
  #app {
    margin: calc((100vh -240px) / 7) auto 0;
  }
}

@media (max-width: 670px) {
  #app {
    margin: calc((100vh -240px) / 9) auto 0;
  }
}

@media (max-width: 570px) {
  #app {
    margin: calc((100vh -240px) / 20) auto 0;
  }
}
</style>



